@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin: 0;
  font-family: "Roboto";
  background-color: #1d2226;
  color: #ddd;
  
}
@media screen and (max-width: 800px){
  .navbar{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .navbar h1,p{
    padding: 5px;
    text-align: center;
  }
}
p{
  color: blue;
  font-weight: bold;
}
.navbar{
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  border-bottom: 2px solid #f2f2f2;
}
.navbar h1{
  color:#D43f3f;
  padding-right: 2em;
  font-size: 60px;
}
.navbar p{
  padding-right: 4em;
  color: #ddd;
}
.navbar .links{
  margin-left: auto;
  
}
.navbar a{
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #5db5fe;
  padding-left: 2em;
}
.navbar a:hover{
  color: #D43f3f;
}
.content{
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/*home bio*/
.home-bio{
  text-align: center;
}
.home-bio h2{
  text-align: left;
  padding-top: 20px;
  padding-bottom: 10px;
  color: #D43f3f;
}
.home-bio li{
  text-align: left;
  padding-bottom: 10px;
}
.home-bio a {
  text-decoration: none;
  color: #5db5fe;
}
.home-bio table{
  table-layout: flex;
  width: 750px;
}
.home-bio th,td{
  padding-bottom: 20px;
  padding-left: 20px;
  text-align: left;
}

/*blog preview*/
.blog-preview{
  padding: 10px 16px;
  padding-top: 15px;
  margin: 20px 0;
  border-bottom: 1px solid #ddd;

}
.blog-preview:hover{
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.1);
}
.blog-preview h2{
  font-size: 25px;
  color: #5db5fe;
  margin-bottom: 8px;
}
.blog-preview a{
  text-decoration: none;
  color:#ddd;
}
#blog-list-h2{
  color:#D43f3f;
  font-size: 40px;
  padding-top: 70px;
}
.blog-preview p{
  color:#ddd;
}
.reference{
  text-align: center;
  padding-top: 15px;
}
.reference a{
  color: #5db5fe;
  text-decoration: underline;
  
}
/*Blog details page*/
.blog-details h2{
  color: #5db5fe;
  font-size: 20px;
  margin-bottom: 10px;
}
.blog-details div{
  color: #ddd;
  font-weight: bold;
  margin: 20px 0;
}
.blog-details button{
  background: #D43f3f;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.blog-details p{
  color: #ddd;
}
.blog-details a{
  text-decoration: none;
  background: #D43f3f;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/*create new blog form*/
.create{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label{
  text-align: left;
  display: block;
}
.create h2{
  font-size: 20px;
  color: #D43f3f;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select{
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
} 
.create button{
  background:#D43f3f;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

